.app {
  width: 1000px;
  margin: 0 auto;
}

.stream {
  width: 100%;
  display: flex;
  border-bottom: 3px solid black;
  &__logo {
    height: 120px;
    width: 120px;
    flex-basis: 120px;
  }
  &__image {
    width: 100%;
    height: 100%;
    display: block;
  }
  &__description {
    text-align: center;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  &__delete {
    flex-basis: 50px;
    display: flex;
    justify-content: center;
    padding-top: 20px;
  }
  &__button {
    height: 20px;
    width: 20px;
    padding: 0;
    cursor: pointer;
  }
}

.online {
  background-color: purple;
}
.offline {
  background-color: grey;
}

.loader {
  font-size: 10px;
  margin: 50px auto;
  text-indent: -9999em;
  width: 11em;
  height: 11em;
  border-radius: 50%;
  background: #800080;
  background: -moz-linear-gradient(left, #800080 10%, rgba(128,0,128, 0) 42%);
  background: -webkit-linear-gradient(left, #800080 10%, rgba(128,0,128, 0) 42%);
  background: -o-linear-gradient(left, #800080 10%, rgba(128,0,128, 0) 42%);
  background: -ms-linear-gradient(left, #800080 10%, rgba(128,0,128, 0) 42%);
  background: linear-gradient(to right, #800080 10%, rgba(128,0,128, 0) 42%);
  position: relative;
  -webkit-animation: load3 1.4s infinite linear;
  animation: load3 1.4s infinite linear;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
}
.loader:before {
  width: 50%;
  height: 50%;
  background: #800080;
  border-radius: 100% 0 0 0;
  position: absolute;
  top: 0;
  left: 0;
  content: '';
}
.loader:after {
  background: #ffffff;
  width: 75%;
  height: 75%;
  border-radius: 50%;
  content: '';
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
@-webkit-keyframes load3 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load3 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
